<template>
  <div>
    <div>
      <input v-model="twitterHandle" placeholder="input a twitter handle">
      <button v-on:click="getTweet">Get Tweets</button>
    </div>
    <div>
    <div class="tweets">
      <ul>
        <li v-for="tweet in tweets" v-bind:key="tweet">
          {{ tweet }}
        </li>
      </ul>
    </div>

    <ul class="list-group">
      <li class="list-group-item">Cras justo odio</li>
      <li class="list-group-item">Dapibus ac facilisis in</li>
      <li class="list-group-item">Morbi leo risus</li>
      <li class="list-group-item">Porta ac consectetur ac</li>
      <li class="list-group-item">Vestibulum at eros</li>
    </ul>
  </div>

  </div>
</template>

<script>
    import $backend from '../backend'

    export default {
      name: 'Tweet',
      data () {
          return {
              twitterHandle: '',
              tweets: {},
              error: ''
          }
      },
      created () {
          console.log("created")
      },
      methods: {
          getTweet () {
              console.log('twitterHandle ' + this.twitterHandle)

              $backend.getTweet(this.twitterHandle).then(responseData => {
                  this.tweets = responseData.data.tweets
              }).catch(error => {
                  console.log(error)
                  this.error = error.message
              })
          }
      }
    }
</script>

